* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	
}

html,
body,
#root,
.App {
	min-height: 100%;
	background-color: #222;
}


.container {
	height: 100%;
	width: 100%;
	
	display: flex;
	align-items: center;
	
	flex-direction: column;
}

.item {
	background-color: red;
	padding: 50px;
	width: 300px;
	border-radius: 5px;
	text-align: center;
}

#btn {
	height: 30px;
	width: 100px;

	font-weight: bold;
}
